<template>  
    <iframe 
    :onload="onloadCode" 
    src="about:blank"></iframe>  
</template>  

<script>  
    export default {  
        props: {
            src: String,
            default: ''
        }, 
        computed: {  
            onloadCode() {
                return `  
                const url = '${this.src}'
                this.contentWindow.document.body.innerHTML = '<video style="width: 100%;height: 100%" controls="controls" src="'+url+'" autoplay></video>';  
                const iframe = top.document.getElementsByTagName('iframe')[0]  
                var evObj = document.createEvent('MouseEvents');  
                evObj.initEvent( 'event1', true, false );  
                iframe.dispatchEvent(evObj)  
                ` 
            },
        },  
        methods: { 
            
        }  
    }  
</script>  

<style lang="scss" scoped>
    iframe{
        border: none;
        width: 100%;
        height: 100%;
    }
</style>  